export const requireHttp=()=>{
    return axiosRequire({
        url:'/queryAll/',
        method:'get'
    })
}
export const requireSelect=(id)=>{
    return axiosRequire({
        url:'/DataSelect',
        method:'get',
        params: {id:id}
    })
}