                    return state.mockData.selectData
                }
            })
        },
        watch: {
            selectData(newValue) {
                if (newValue.length!==0){
                    this.h2s = newValue.map(d => d.h2s)
                    this.ch4 = newValue.map(d => d.ch4)
                    this.time = newValue.map(d => d.time.toString())
                    if (this.$echarts.getInstanceByDom(document.querySelector('.echarts4'))===undefined) {
                        this.createEcharts()
                    } else {
                        myEchart.setOption({
                            title: {
                                text: `${this.selectData[0].id}号设备气体检测情况`,
                            },
                            xAxis: [
