                                    })
                                }
                            ],
                            series: [
                                {
                                    data: this.h2s
                                },
                                {
                                    data: this.ch4
                                }
                            ]
                        })
                    }
                    this.deleteIsShow = true
                }else{
                    alert('输入的id不存在')
                }

            }
        }
    }
