import {requireHttp,requireSelect} from '../../api/index'


const state={
    jiangYangData:{},
    selectData:[]
}
const actions={
    async getJiangYangData(commit){
        const data=await requireHttp()
        commit.commit('GETJIANGYANGDATA',data)
    },
    async getSelectData(commit,id){
        const data=await requireSelect(id)
        commit.commit('GETSELECTDATA',data)
    }
}
const mutations={
    GETJIANGYANGDATA(state,value){
        state.jiangYangData=value
    },
    GETSELECTDATA(state,value){
        state.selectData=value.data
    }
}

export default {
    state,
    actions,
    mutations,
}